html,
body {
  overflow-x: hidden;
}

* {
  margin: 0;
  padding: 0;
}

.movie-name {
  padding: 50px 15px 0px;
}

.movie-name h1 {
  font-size: 48px;
  font-weight: 500;
  text-align: center;
}

.searchbox {
  display: block;
  width: 100%;
  padding: 15px;
  border: none;
  outline: none;
  background: none;
  background-color: #eee;
  border-radius: 8px;
  color: #333333;
  font-size: 20px;
  font-weight: 300;
  transition: 0.4s ease-out;
}
.searchbox:focus {
  box-shadow: 0px opx 8px 3px #4484c4;
}
.nav-links a {
  color: #333333;
  text-decoration: none;
  font-size: small;
  padding: 10px 25px 20px 25px;
}

/* .nav-links a:hover:not(.active) {
  background-color: red;
} */

.nav-links a:focus {
  background-color: #ffffff;

  border-top-style: solid;
  border-top-color: coral;
}

.content {
  display: flex;
  margin: auto;
  text-align: center;
}

.logoimg {
  height: 40px;
  width: 30px;
  margin-top: 5px;
}

.name-count h3 {
  color: #0366d6;
}
.name-tag {
  font-size: small;
}
.name-count-row {
  background-color: #fafbfc;
  padding: 10px 0 10px 0;
}
.navbar {
  margin-bottom: 0;
}
.blog-content {
  height: 100vh;
}
